import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HashLocationStrategy, LocationStrategy } from '@angular/common';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { RouterModule, Routes } from '@angular/router';
import 'hammerjs';

import { FuseModule } from '@fuse/fuse.module';
import { FuseSharedModule } from '@fuse/shared.module';
import { fuseConfig } from './fuse-config';

import { AppComponent } from './app.component';
import { FuseMainModule } from './main/main.module';
import { NotFoundModule } from './main/content/not-found/not-found.module';
import { LoginModule } from './main/content/login/login.module';

import { AngularFireModule } from 'angularfire2';
import { AngularFireAuthModule } from 'angularfire2/auth';
import { AngularFirestoreModule } from 'angularfire2/firestore';
import { AuthGuardService } from './main/content/login/auth-guard.service';

const appRoutes: Routes = [
    {
        path: '',
        redirectTo: 'users',
        pathMatch: 'full'
    },
    {
        path        : 'comments',
        canLoad: [AuthGuardService],
        loadChildren: './main/content/comments/comments.module#CommentsModule'
    },
    {
        path        : 'users',
        canLoad: [AuthGuardService],
        loadChildren: './main/content/users/users.module#UsersModule'
    },
    {
        path      : '**',
        redirectTo: '404'
    },
];

@NgModule({
    declarations: [
        AppComponent
    ],
    imports     : [
        BrowserModule,
        BrowserAnimationsModule,
        RouterModule.forRoot(appRoutes, { useHash: true }),

        // Fuse Main and Shared modules
        FuseModule.forRoot(fuseConfig),
        FuseSharedModule,
        FuseMainModule,
        NotFoundModule,
        LoginModule,

        AngularFireModule.initializeApp({
            apiKey: "AIzaSyBlqWTVqH0OG_Pu4QMfJLr-TD7Jk7nDaW8",
            authDomain: "ngx-fuse.firebaseapp.com",
            databaseURL: "https://ngx-fuse.firebaseio.com",
            projectId: "ngx-fuse",
            storageBucket: "",
            messagingSenderId: "989974715446"
        }),
        AngularFireAuthModule,
        AngularFirestoreModule
    ],
    providers: [
        AuthGuardService,
        { provide: LocationStrategy, useClass: HashLocationStrategy }
    ],
    bootstrap   : [
        AppComponent
    ]
})
export class AppModule
{
}



